.message-form {
  border: 2px solid black;
  width: 100%;
  max-width: 500px;
  padding: 2rem;
  border-radius: 8px;
  background: #f6ecdb;

  @media (min-width: 768px) {
    max-width: 740px;
    padding: 2.5rem;
  }

  @media (min-width: 1024px) {
    max-width: 980px;
  }

  @media (min-width: 1200px) {
    max-width: 1170px;
  }

  form {
    .form-group {
      margin-bottom: 1.8rem;
      position: relative;

      &:last-child {
        margin-bottom: 0;
      }

      input,
      textarea {
        width: 100%;
        padding: 1rem;
        border: none;
        border-radius: 6px;
        font-size: 1rem;
        font-weight: 500;
        letter-spacing: 0.5px;
        background-color: #f8f9fa;
        box-shadow: inset 0 1px 2px rgba(0, 0, 0, 0.1);
        transition: all 0.25s ease;

        &::placeholder {
          color: #658443;
          text-transform: uppercase;
          font-size: 0.85rem;
          letter-spacing: 1px;
        }

        &:focus {
          outline: none;
          background-color: white;
          box-shadow: 0 0 0 3px rgb(101, 132, 67),
                      inset 0 1px 2px rgba(0, 0, 0, 0.1);
        }
      }

      textarea {
        min-height: 150px;
        resize: vertical;
      }

      // Estilos para errores
      &.error {
        input,
        textarea {
          box-shadow: 0 0 0 3px rgba(220, 53, 69, 0.25);
        }
      }

      .error-message {
        color: #dc3545;
        font-size: 0.8rem;
        margin-top: 0.5rem;
        display: none;
      }

      &.error .error-message {
        display: block;
      }     
    }
    .submit-button {
        text-align: center;        
    }
  }
}